* {
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  overflow-x: hidden;
}

body {
  background-color: black;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.App {
  width: 100%;
}

.header {
  background-color: rgba(0, 0, 0, 0.7);
  height: 60px;
  width: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  backdrop-filter: blur(9px);
  -webkit-backdrop-filter: blur(9px);
}

.logoContentCotainer {
  width: 100%;
  display: flex;
  align-items: unset;
  justify-content: unset;
  padding: 0;
  overflow: hidden;
}

.headerBlur {
  height: 60px;
  width: 100%;
  background-color: black;
}

.logoContainer {
  height: 60px;
  width: 60px;
  padding: 10px;
  overflow: hidden;
}

.navBarContainer {
  width: calc(100% - 120px);
}

.navBar ul {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 60px;
}

.navBar ul li a {
  color: white;
  font-size: 1.1em;
  font-weight: bold;
  transition: 0.2s all;
}

.navBar ul li a:hover {
  color: #dddddd;
}

.hero {
  height: fit-content;
  width: 100%;
  display: flex;
  justify-content: center;
}

.heroImg {
  /* max-height: calc(100vh - 60px); */
  height: fit-content;
  width: 100%;
  min-width: 100%;
  /* background-color: red; */
}

.heroImg video {
  height: 100%;
  width: 100%;
  max-width: 100%;
  max-height: calc(100vh - 60px);
  min-height: 288px;
  /* display: none; */
}

.heroContent {
  margin-left: 50%;
  position: relative;
  z-index: 2;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.heroContent h2 {
  color: white;
  text-align: center;
}

.productElement {
  height: calc(100vh - 60px);
  min-height: 500px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.productElement p {
  text-align: center;
  width: 18rem;
}

.productImgContainer {
  height: 50%;
  display: flex;
  background: blue;
}

.productImgContainer img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.btn {
  font-size: 1.1em;
  border: none;
  background-color: #5700ff;
  color: white;
  padding: 10px;
  border-radius: 9px;
  margin: 10px;
  cursor: pointer;
}

.btn:hover {
  background-color: #5600ee;
}

.mobileNav,
.menuIconContainer {
  display: none;
}

.footerContainer {
  background-color: black;
  height: 100vh;
  width: 100%;
  color: white;
  padding-top: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.footerHeader {
  width: 100%;
  display: flex;
  justify-content: center;
}

.footerHeader svg {
  height: 75px;
}

.footerLists {
  display: flex;
  justify-content: space-between;
}

.footerLists ul {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footerLists ul li {
  padding: 20px;
}

.footerLists ul li a {
  color: white;
}

.footerLists ul li a:hover {
  color: #dddddd;
  transition: 0.2s all;
}

.footerEnd {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footerEnd ul {
  padding: 0;
}

.footerEnd ul li {
  text-align: center;
}

.credits {
  margin-top: 10px;
  color: #bbbbbb;
}

@media only screen and (max-width: 600px) {
  .navBarContainer {
    display: none;
  }

  .header {
    justify-content: center;
  }

  .logoContentCotainer {
    width: calc(100% - 60px);
    padding-right: 60px;
    justify-content: center;
  }

  .menuIconContainer {
    height: 60px;
    width: 60px;
    display: block;
    background: none;
    left: 0;
    padding: 15px;
    border: none;
  }

  .menuIcon {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
  }

  .menuIconContainer:hover {
    background-color: rgba(30, 30, 30, 0.5);
    cursor: pointer;
  }

  .menuBar {
    height: 5px;
    width: 100%;
    display: block;
    background: rgb(200, 200, 200);
    transition: 0.5s all;
  }

  .clicked:nth-child(1) {
    -webkit-transform: rotate(45deg);
    -webkit-transform-origin: center left;
    transform: rotate(45deg);
    transform-origin: center left;
    width: calc(100% + 6px);
  }

  .clicked:nth-child(2) {
    transform: translateX(-40px);
    background-color: rgba(0, 0, 0, 0);
  }

  .clicked:nth-child(3) {
    transform: rotate(-45deg);
    transform-origin: center left;
    width: calc(100% + 6px);
  }

  .mobileNav {
    height: calc(100vh - 60px);
    width: 100%;
    background-color: black;
    position: fixed;
    z-index: 10;
    overflow-y: scroll;
    display: block;
    transition: 0.5s all;
  }

  .mobileNavHidden {
    /* display: none; */
    height: 0vh;
  }

  .mobileNav ul {
    float: left;
    padding: 0;
    width: 100%;
  }

  .mobileNav ul li {
    width: 100%;
    padding: 10px;
    display: flex;
    justify-content: center;
    font-size: 1.1em;
    border-bottom: 1px #777777 solid;
  }

  .mobileNav ul li a {
    color: white;
  }

  .mobileNav ul li a:hover {
    color: #dddddd;
  }
}
